<template>
  <div>
    <h4>{{multilanguage.checkandfill[language]}}</h4>
    <div v-if="loader" class="progress">
          <div class="indeterminate"></div>
    </div>
    <div v-if="detalles">
        <form>
          <ul class="collapsible">
            <li class="active">
              <div class="collapsible-header"><h5>{{multilanguage.detalles[language]}}</h5></div>
              <div class="collapsible-body">
                <div class="row">
                  <div class="col m6 s12">
                    <h6>{{multilanguage.totales[language]}}</h6>
                    <table class="striped">
                      <thead>
                        <tr>
                          <th>{{multilanguage.neto[language]}}</th>
                          <th>{{multilanguage.venta[language]}}</th>
                          <th>{{multilanguage.comision[language]}}</th>
                          <th>{{multilanguage.tipo[language]}}</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="price in detalles.prices">
                          <td> {{ price.netPrice.value }} {{ price.netPrice.currencyIsoCode}}</td>
                          <td v-if="price.retailPrice" > {{ price.retailPrice.value }} {{ price.retailPrice.currencyIsoCode}}</td><td v-else></td>
                          <td v-if="price.Commision" > {{ price.Commision.value }} {{ price.Commision.currencyIsoCode}}</td><td v-else></td>
                          <td> {{ price.type}}</td>
                         </tr>
                      </tbody>
                    </table>
                  </div>
                  <div v-if="false" class="col m6 s12">
                    <h6>{{multilanguage.suplementodispo[language]}}</h6>
                    <table>
                      <thead>
                        <tr>
                          <th>{{multilanguage.descripcion[language]}}</th>
                          <th>{{multilanguage.coste[language]}}</th>
                          <th width="60">{{multilanguage.seleccionado[language]}}</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="c in detalles.availableServices">
                          <td>{{c.description}}</td>
                          <td>{{ c.retailPrice.value }} {{ c.retailPrice.currencyIsoCode}}</td>
                          <td>
                            <select v-model="c.cantidad">
                              <option value="0">0</option>
                              <option value="1">1</option>
                              <option value="2">2</option>
                              <option value="3">3</option>
                              <option value="4">4</option>
                            </select></td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
                <div class="row">
                  <h6>{{multilanguage.lineapago[language]}}</h6>
                  <table>
                    <thead>
                      <tr>
                        <th>{{multilanguage.fecha[language]}}</th>
                        <th>{{multilanguage.coste[language]}}</th>
                        <th>{{multilanguage.tipo[language]}}</th>
                      </tr>
                    </thead>
                    <tbody >
                      <tr v-for="p in detalles.paymentLines">
                        <td>{{p.date}}</td><td> {{p.amount.value }} {{p.amount.currencyIsoCode }}</td><td>{{p.paymentMethod}}</td>
                      </tr>
                    </tbody>
                    </table>
                </div>
                <div class="row">
                  <div class="col m6 s12">
                    <h6>{{multilanguage.observaciones[language]}}</h6>
                    <div v-for="r in detalles.remarks">
                      {{ r.text }}
                    </div>
                  </div>
                  <div class="col m6 s12">
                    <h6>{{multilanguage.costecancelacion[language]}}</h6>
                    <table>
                      <thead>
                        <tr>
                          <th>{{multilanguage.tras[language]}}</th>
                          <th>{{multilanguage.coste[language]}}</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="c in detalles.cancellationCosts">
                          <td>{{c.gmttime}}</td>
                          <td>{{ c.net.value }} {{ c.net.currencyIsoCode}}</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </li>
            <li>
              <div class="collapsible-header"><h5>{{multilanguage.rellenartitular[language]}}</h5></div>
              <div class="collapsible-body">
                <div class="col s12">
                  <div>
                    <label for="name">{{multilanguage.nombre[language]}}</label>
                    <input type="text" id="name" :placeholder="multilanguage.nombre[language]" v-model="titular">
                  </div>
                  <div>
                    <label for="comentarios">{{multilanguage.peticionespecial[language]}}</label>
                    <textarea id="comentarios" placeholder="" v-model="comentarios"></textarea>
                  </div>
                </div>
              </div>
            <li>
              <div class="collapsible-header"><h5>{{multilanguage.informarocupacion[language]}}</h5></div>
              <div class="collapsible-body">
                <div class="col s12">

                  <div v-for="(oc, index1) in ocupacion">
                    <h6>{{oc.roomName}}</h6>
                    <div v-for="(n, index2) in oc.occupancy">
                      <h6>Client {{index2 + 1}}</h6>
                      <!-- {{ocupacion[index1]}} -->
                      <label :for="'name' + index1 + index2">{{multilanguage.nombre[language]}}</label>
                      <input type="text" :id="'name' + index1 + index2" placeholder="" v-model="ocupacion[index1].occupancy[index2].name">
                      <label :for="'surname' + index1 + index2">{{multilanguage.apellido[language]}}</label>
                      <input type="text" :id="'surname' + index1 + index2" placeholder="" v-model="ocupacion[index1].occupancy[index2].surname">
                      <label :for="'age' + index1 + index2">{{multilanguage.edad[language]}}</label>
                      <input type="text" :id="'age' + index1 + index2" placeholder="" v-model="ocupacion[index1].occupancy[index2].age">
                      <label :for="'type' + index1 + index2">{{multilanguage.tipo[language]}}</label>
                      <input type="text" :id="'type' + index1 + index2" placeholder=" " v-model="ocupacion[index1].occupancy[index2].type">
                    </div>
                  </div>
                </div>
               </div>
            </li>
            <li v-if="requestPayment===true">
              <div class="collapsible-header"><h5>{{multilanguage.informacionpago[language]}}</h5></div>
              <div class="collapsible-body">
                <div class="col s12">
                  <span>This information is for ...</span>
                  <br/>
                  <label for="cardHolderName">{{multilanguage.nombretitulartarjeta[language]}}</label>
                  <input type="text" id="cardHolderName" placeholder="" v-model="creditCard.cardHolderName">
                  <label for="cardNumber">{{multilanguage.numerotarjeta[language]}}</label>
                  <input type="text" id="cardNumber" placeholder="" v-model="creditCard.cardNumber">
                  <label for="cardCVC">{{multilanguage.cvc[language]}}</label>
                  <input type="text" id="cardCVC" placeholder="" v-model="creditCard.cardCVC">

                  <h6>cardType esto deberia ser un desplegable</h6>

                  <label for="expiryDate">{{multilanguage.caducidadtarjeta[language]}}</label>
                  <input type="text" id="expiryDate" placeholder="(mmyy)" v-model="creditCard.expiryDate">
                  <label for="email">{{multilanguage.email[language]}}</label>
                  <input type="text" id="email" placeholder="" v-model="creditCard.email">
                  <label for="phoneNumber">{{multilanguage.numerotelefono[language]}}</label>
                  <input type="text" id="phoneNumber" placeholder="" v-model="creditCard.phoneNumber">
                </div>
              </div>
            </li>
          </ul>

      </form>
    </div>
    <a class="waves-effect waves-light btn" v-on:click="book">{{multilanguage.reservar[language]}}</a>
    <div class="cantidad"></div>
  </div>
</template>

<script>

import axios from 'axios'

var  globallanguage = 'es'
if (globaldata.language != null && globaldata.language != '')
{
   globallanguage =  globaldata.language
}
const langfile = require("../../assets/lang/content.json")

export default {
  name: 'Detalle',
  data: function () {
    return {
      detalles: null,
      titular: null,
      comentarios: null,
      ocupacion: [],
      requestPayment: null,
      creditCard: {
        cardHolderName: null,
        cardNumber: null,
        cardCVC: null,
        cardType: null,
        expiryDate: null,
        email: null,
        phoneNumber: null
      },
      loader: null,
      language: globaldata.language,
      multilanguage:langfile.multilanguage
    }
  },
  methods: {
    procesar: function (response) {
      if (response.data.statusCode === 200) this.$router.push({ name: 'ok', query: { bookingId: response.data.bookingId } })
      else this.$router.push({ name: 'ko', query: { msg: response.data.msg } })
    },
    book: function (e) {
      console.log(this.$data.ocupacion)
      console.log(this.$data.detalles)
      var esto = this
      var d = this.$data
      scroll(0,0)
      d.loader = true
      axios({
        method: 'put',
        url: globaldata.baseurl + '/hotel/booking',
        data: {
          // language: globaldata.language,
          rateKeys: this.$data.ocupacion,
          bookingReference: '',
          leadName: this.$data.titular,
          commentsToProvider: this.$data.comentarios,
          privateComments: '',
          services: []
        }
      })
        .then(function (response) {
          console.log(response.data)
          d.loader = false
          esto.procesar(response)
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  },
  mounted () {

    console.log('cambios')
    var d = this.$data
    d.loader = true
    axios.post(
      globaldata.baseurl + '/hotel/pricedetails',
      this.$route.query.keys,
      {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        }
      })
      .then(function (response) {
        d.loader = null
        console.log(response.data)
        d.detalles = response.data
        for (var i = 0; i < d.detalles.rateKeys.length; i++) {
          var ocup = {}
          var paxDetails = []
          for (var j = 0; j < d.detalles.rateKeys[i].paxPerRoom; j++) {
            paxDetails.push({
              name: null,
              surname: null,
              age: null,
              type: null
            })
          }

          ocup.occupancy = paxDetails
          ocup.roomId = i + 1
          ocup.rateKey = d.detalles.rateKeys[i].key
          ocup.roomName = d.detalles.rateKeys[i].roomName
          ocup.requestPaymentData = d.detalles.rateKeys[i].requestPaymentData

          if (d.detalles.rateKeys[i].requestPaymentData) {
            d.requestPayment = true
          }

          console.log('rk.roomName' + ocup.roomname)
          console.log('rk.key' + ocup.ratekey)

          console.log('rk' + i)
          d.ocupacion.push(ocup)
        }
        setTimeout(function () {
          var elems = document.querySelectorAll('.collapsible')
          var instances = M.Collapsible.init(elems, {accordion: false})
          console.log(elems)
        })
      })
      .catch(function (error) {
        console.log(error)
        d.loader = null
      })
  }
}
</script>

<style scoped>

  a.btn {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }

</style>



