import Vue from 'vue'
import Router from 'vue-router'
import Reserva from '@/components/Reserva'
import Hotel from '@/components/hotel/Hotel'
import HotelFormulario from '@/components/hotel/Formulario'
import HotelDispo from '@/components/hotel/Dispo'
import HotelRates from '@/components/hotel/Rates'
import HotelDetalle from '@/components/hotel/Detalle'
import Ok from '@/components/Ok'
import Ko from '@/components/Ko'
import Config from '@/components/Config'
import Login from '@/components/Login'
import Welcome from '@/components/Welcome'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'reserva',
      component: Reserva,
      children:
      [

        {
          path: '',
          name: 'hotel_formulario',
          component: HotelFormulario
        },

        {
          path: 'dispo',
          name: 'hotel_dispo',
          component: HotelDispo
        },
        {
          path: 'rates',
          name: 'hotel_rates',
          component: HotelRates
        },
        {
          path: 'detalle',
          name: 'hotel_detalle',
          component: HotelDetalle
        },

        {
          path: 'ok',
          name: 'ok',
          component: Ok
        },
        {
          path: 'ko',
          name: 'ko',
          component: Ko
        },
        {
          path: 'config',
          name: 'config',
          component: Config
        },
        {
          path: 'login',
          name: 'login',
          component: Login
        },
        {
          path: 'welcome',
          name: 'welcome',
          component: Welcome
        }
      ]
    }

  ]
})



