<template>
<div>
  <div v-if="loader" >
    <h4>{{multilanguage.obteniendoresultados[language]}}</h4>
    <div class="progress">
      <div class="indeterminate"></div>
    </div>
  </div>
  <div v-if="dispo">
    <ul class="collection with-header">
      <li class="collection-header"><h4>{{ dispo.msg }}</h4></li>
      <a v-on:click="abrirdetalle(hotel.hotelKey)" v-for="hotel in dispo.hotels" style="cursor: pointer;">
        <li class="collection-item">
          <div>{{ hotel.hotelName }}<a class="secondary-content">{{ hotel.bestDeal.retailPrice.value }} {{ hotel.bestDeal.retailPrice.currencyIsoCode }}</a></div>
        </li>
      </a>
    </ul>
  </div>
</div>
</template>

<script>
import axios from 'axios'

var  globallanguage = 'es'
if (globaldata.language != null && globaldata.language != '')
{
   globallanguage =  globaldata.language
}
const langfile = require("../../assets/lang/content.json")

export default {
  name: 'HotelDispo',
  data: function () {
    return {
      dispo: null,
      loader: null,
      language: globaldata.language,
      multilanguage:langfile.multilanguage
    }
  },
  methods: {
    abrirdetalle (k) {
      console.log(k)
      this.$router.push({ name: 'hotel_rates', query: {hotelkey: k} })
    }
  },
  mounted () {

    console.log(this.$route.query)
    var d = this.$data
    d.loader = true
    axios({
      method: 'get',
      url: globaldata.baseurl + '/hotel/available',
      params: {
        language: globaldata.language,
        resorts: this.$route.query.destino,
        checkin: this.$route.query.entrada? this.$route.query.entrada.replace(/-/g, ''): null,
        checkout: this.$route.query.salida? this.$route.query.salida.replace(/-/g, ''): null,
        occupancies: this.$route.query.ocupacion
      }
    })
      .then(function (response) {
        d.loader = null
        console.log(response.data)
        d.dispo = response.data
        // Vue.set(vm, 'dispo.data', response.data)
      })
      .catch(function (error) {
        d.loader = null
        console.log(error)
      })
  }
}
</script>

<style scoped>

</style>



