<template>
  <div>
    <div v-if="loader" >
      <div class="center-align">
        <h4>Getting rooms ...</h4>
        <div class="preloader-wrapper active ">
          <div class="spinner-layer ">
            <div class="circle-clipper left">
              <div class="circle"></div>
            </div><div class="gap-patch">
              <div class="circle"></div>
            </div><div class="circle-clipper right">
              <div class="circle"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="rates">
    <h4>Choose your rooms</h4>
    <!--<ul class="collection with-header" >
      <li class="collection-header"><h4>{{ rates.msg }}</h4></li>
      <li class="collection-item" v-for="ocupacion in rates.rates"><div>{{ ocupacion.numberOfRooms }} x {{ ocupacion.paxPerRoom }} ({{ ocupacion.ages }}):</div>
        <div>
          <select v-model="ocupacion.seleccionado">
            <template v-for="aloc in ocupacion.options">
              <option v-for="price in aloc.prices" :value="price.key">{{ aloc.roomName }} {{ price.boardBasisName }} <template v-if="price.retailPrice">{{ price.retailPrice.value }} {{ price.retailPrice.currencyIsoCode }}</template>
                <template v-else>{{ price.netPrice.value }} {{ price.netPrice.currencyIsoCode }}</template></option>
            </template>
          </select>
        </div>
      </li>
    </ul>-->
    <ul>
      <li class="collection-header"><h4>{{ rates.msg }}</h4></li>
      <div class="divider"></div>
      <li class="collection-item" v-for="ocupacion in rates.rates"><div><h6>{{ ocupacion.numberOfRooms }} x {{ ocupacion.paxPerRoom }} ({{ ocupacion.ages }}):</h6></div>
        <div>
          <select class="scrollable" v-model="ocupacion.seleccionado">
            <template v-for="aloc in ocupacion.options">
              <option v-for="price in aloc.prices" :value="price.key">{{ aloc.roomName }} {{ price.boardBasisName }} <template v-if="price.retailPrice">{{ price.retailPrice.value }} {{ price.retailPrice.currencyIsoCode }}</template>
                <template v-else>{{ price.netPrice.value }} {{ price.netPrice.currencyIsoCode }}</template></option>
            </template>
          </select>
        </div>
        <div class="divider"></div>
      </li>
    </ul>

    <a class="waves-effect waves-light btn" v-on:click="abrirdetalle">Go on</a>
    </div>

  </div>

</template>

<script>
import axios from 'axios'

var  globallanguage = 'es'
if (globaldata.language != null && globaldata.language != '')
{
   globallanguage =  globaldata.language
}
const langfile = require("../../assets/lang/content.json")

export default {
  name: 'Rates',
  data: function () {
    return {
      rates: null,
      loader: null,
      language: globaldata.language,
      multilanguage:langfile.multilanguage
    }
  },
  methods: {
    abrirdetalle: function (e) {
      console.log('------------------')
      var s
      for (var z in this.$data.rates.rates) {
        if (s) s += ','
        else s = ''
        s += this.$data.rates.rates[z].seleccionado
      }
      this.$router.push({ name: 'hotel_detalle', query: {keys: s} })
    }
  },
  mounted () {
    console.log('Entra en mis mod' + this.$route.query)
    var d = this.$data
    d.loader = true
    axios.post(globaldata.baseurl + '/hotel/rates',
      this.$route.query.hotelkey,
      {
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        }
      }
    )
      .then(function (response) {
        d.loader = null
        console.log(response.data)
        for (var z in response.data.rates) {
          response.data.rates[z].seleccionado = response.data.rates[z].options[0].prices[0].key
        }
        d.rates = response.data
        // Vue.set(vm, 'dispo.data', response.data)
        setTimeout(function () {
          console.log(d.opciones)
          var elems = document.querySelectorAll('select')
          var instances = M.FormSelect.init(elems, {})
        })
      })
      .catch(function (error) {
        d.loader = null
        console.log(error)
      })
  }
}
</script>

<style scoped>
  a.btn {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
    .select-dropdown{
          overflow-y: auto !important;
      }
</style>



